import React from 'react';
import Board from '../Board/Board';
import Pawn from './Pawn';
import Rook from './Rook';
import Knight from './Knight';
import Bishop from './Bishop';
import King from './King';
import Queen from './Queen';
import CapturedPieces from './CapturedPieces';
import TurnIndicator from './TurnIndicator';
import CheckDisplay from './CheckDisplay';
import TestDisplay from './TestDisplay';

class Game extends React.Component{
  constructor(props){
    super(props);

    //set the player color
    const player = "white";
    const opponent = "black";
    //construct the initial board configuration
    let initial = [];
    //boolean to create alternating tile colors
    let isBlack = false;
    let playerKing;
    let opponentKing;

    //this for loop creates a row each iteration
    for(let i = 0; i < 8; i++)
    {
      //this inner loop creates a tile each iteration
      let curr_row = [];
      for(let l = 0; l < 8; l++)
      {
        let curr_square = "black square";
        if(!isBlack)
            curr_square = "white square";
        curr_row = [...curr_row, {color: curr_square, component:null, value: null, initColor: curr_square}];
        isBlack = !isBlack; 
      }
      initial = [...initial, curr_row];
      isBlack = !isBlack;
    }

    //initialize the pawns
    for(let j = 0; j < 8; j++)
    {
      const pawnID = "pawn" + j;
      initial[6][j] = {...initial[6][j], value:{piece: "pawn", color:player, ID: pawnID} ,component:(
      <Pawn m_color={player} m_position={[6,j]} />
      )};
       initial[1][j] = {...initial[1][j], value:{piece: "pawn", color:opponent, ID:pawnID} ,component:(
      <Pawn m_color={opponent} m_position={[1,j]} />
      )};
    }

    //initalize the other pieces for the player
    //initialize rooks
    initial[7][0] =  {...initial[7][0], 
      value: {piece: "rook", color: player, ID:"rook0"}, component:(
        <Rook m_color={player} m_position={[7,0]}/>
        )};
    initial[7][7] =  {...initial[7][7], 
      value: {piece:"rook", color: player, ID:"rook1"},component:(
        <Rook m_color={player} m_position={[7,7]}/>
        )};
    //initialize knights
    initial[7][1] = {...initial[7][1],
      value: {piece:"knight", color: player, ID:"knight0"}, component: (
        <Knight m_color={player} m_position={[7,1]}/>
        )};
    initial[7][6] = {...initial[7][6],
      value: {piece: "knight", color: player, ID:"knight1"}, component: (
        <Knight m_color={player} m_position={[7,6]}/>
        )};
    //initialize bishops
    initial[7][2] = {...initial[7][2],
      value: {piece: "bishop", color: player, ID:"bishop0"}, component: (
        <Bishop m_color={player} m_position={[7,2]}/>
        )};
    initial[7][5] = {...initial[7][5],
      value: {piece: "bishop", color: player, ID:"bishop1"}, component: (
        <Bishop m_color={player} m_position={[7,5]}/>
        )};

    //initialize the other pieces for the opponent
    initial[0][0] =  {...initial[0][0], 
      value: {piece: "rook", color: opponent, ID:"rook0"}, component: (
        <Rook m_color={opponent} m_position={[0,0]}/>
        )};
    initial[0][7] =  {...initial[0][7], 
      value: {piece: "rook", color: opponent, ID:"rook1"}, component: (
        <Rook m_color={opponent} m_position={[0,7]}/>
        )};
    //initialize knights
    initial[0][1] = {...initial[0][1],
      value: {piece: "knight", color: opponent, ID:"knight0"}, component:(
        <Knight m_color={opponent} m_position={[0,1]}/>
        )};
    initial[0][6] = {...initial[0][6],
      value: {piece: "knight", color: opponent, ID:"knight1"}, component:(
        <Knight m_color={opponent} m_position={[0,6]}/>
        )};
    //initialize bishops
    initial[0][2] = {...initial[0][2],
      value: {piece: "bishop", color: opponent, ID:"bishop0"}, component: (
        <Bishop m_color={opponent} m_position={[0,2]}/>
        )};
    initial[0][5] = {...initial[0][5],
      value: {piece: "bishop", color: opponent, ID:"bishop1"}, component: (
        <Bishop m_color={opponent} m_position={[0,5]}/>
        )};
    let playerQueen;
    let opponentQueen;
    if(player === "white")
    {
      //initialize opponent king and queen
      initial[0][3] = {...initial[0][3],
        value: {piece: "queen", color:opponent, ID:"queen"}, component: (
          <Queen m_color={opponent} m_position={[0,3]}/>
          )};
      initial[0][4] = {...initial[0][4],
        value: {piece: "king", color:opponent, ID:"king" }, component: (
          <King m_color={opponent} m_position={[0,4]}/>
          )};

      //initialize player king and queen
      initial[7][3] = {...initial[7][3],
        value: {piece:"queen",color: player, ID:"queen"}, component: (
          <Queen m_color={player} m_position={[7,3]}/>
          )};
      initial[7][4] = {...initial[7][4],
        value: {piece: "king", color: player, ID:"king"}, component: (
          <King m_color={player} m_position={[7,4]}/>
          )};
      playerKing = [7,4];
      opponentKing = [0,4];  
      playerQueen = [7,3];
      opponentQueen = [0,3];    
    }else{
      //initalize opponent king and queen
      initial[0][3] = {...initial[0][3],
        value:{piece: "king", color: opponent, ID:"king"}, component: (
          <King m_color={opponent} m_position={[0,3]}/>
          )};
      initial[0][4] = {...initial[0][4],
        value: {piece: "queen", color: opponent, ID:"queen"}, component: (
          <Queen m_color={opponent} m_position={[0,4]}/>
          )};

      //initialize player king and queen
      initial[7][3] = {...initial[7][3],
        value:{piece:"king", color: player, ID:"king"}, component: (
          <King m_color={player} m_position={[7,3]}/>
          )};
      initial[7][4] = {...initial[7][4],
        value: {piece:"queen", color: player, ID:"queen"}, component: (
          <Queen m_color={player} m_position={[7,4]}/>
          )};
      playerKing = [7,3];
      opponentKing = [0,3];
      playerQueen = [7,4];
      opponentQueen = [0,4];
    }

    // let playerCover = {rook0: {loc:[7,0],cover:[]}, rook1: {loc:[7,7],cover:[]}, knight0: {loc:[7,1],cover:[[5,0], [5,2]]}, knight1: {loc:[7,6],cover:[[5,7], [5,5]]}, bishop0: {loc:[7,2],cover:[]}, bishop1: {loc:[7,5],cover:[]}, queen: {loc:playerQueen,cover:[]}};
    // let opponentCover = {rook0: {loc:[0,0],cover:[]}, rook1: {loc:[0,7],cover:[]}, knight0: {loc:[0,1],cover:[[2,0],[2,2]]}, knight1: {loc:[0,6],cover:[[2,7],[2,5]]}, bishop0: {loc:[0,2],cover:[]}, bishop1: {loc:[0,5],cover:[]}, queen: {loc:opponentQueen,cover:[]}};

    let playerPieces = {rook0:[7,0], rook1:[7,7], knight0: [7,1], knight1:[7,6], bishop0:[7,2], bishop1: [7,5], queen: playerQueen, king:playerKing};
    let opponentPieces = {rook0:[0,0], rook1:[0,7], knight0:[0,1], knight1: [0,6], bishop0:[0,2], bishop1:[0,5], queen: opponentQueen, king:opponentKing};

    //set the cover for pawns (both player and opponent)
    for(let i = 0; i < 8; i++)
    {
      playerPieces["pawn" + i] = [6,i];
      opponentPieces["pawn" + i] = [1,i];      
    }

    this.state = {
      history:[
      {tiles: initial}
      ],
      stepNumber: 0,
      playerIsNext: true,
      player: player,
      opponent: opponent,
      rank: 8,
      current: null,
      capturedPieces: [],
      playerPieces: playerPieces,
      opponentPieces: opponentPieces,
      playerKing: playerKing,
      opponentKing: opponentKing,
      playerCheck: false,
      opponentCheck: false,
      highlightedTiles: [],
    };
    
  }

  //row and col is the location of the pawn
  generatePawnCover = (row, col, color, tiles) =>
  {
    let legalMovesPawn = [];
    if(color === this.state.player)
    {
      if(row > -1 && row < 8 && col > -1 && col < 8)
      {
        //can potentially move forward by 1 or capture
        if(row-1 > -1 && tiles[row-1][col].value === null){
          legalMovesPawn = [...legalMovesPawn, [row-1, col]];
        }
        if(row-1 > -1 && col+1 < 8 && tiles[row-1][col+1].value !== null)
        {
          if(tiles[row-1][col+1].value.color !== color)
          {
            legalMovesPawn = [...legalMovesPawn, [row-1, col+1]];    
          }
        }
        if(row-1 > -1 && col - 1 > -1 && tiles[row-1][col-1].value !== null)
        {
          if(tiles[row-1][col-1].value.color !== color)
          {
            legalMovesPawn = [...legalMovesPawn, [row-1, col-1]];    
          }
        }
        if(row === 6)
        {
          if(tiles[row-1][col].value === null && tiles[row-2][col].value === null){
            legalMovesPawn = [...legalMovesPawn, [row-2, col]];
          }
        }
      }
    }else if(color === this.state.opponent)
    {
      //can potentially move forward by 1 or capture
      if(row > -1 && row < 8 && col > -1 && col < 8)
      {
        if(row+1 < 8 && tiles[row+1][col].value === null){
          legalMovesPawn = [...legalMovesPawn, [row+1, col]];
        }
        if(row + 1 < 8 && col + 1 < 8 && tiles[row+1][col+1].value !== null)
        {
          if(tiles[row+1][col+1].value.color !== color)
          {
            legalMovesPawn = [...legalMovesPawn, [row+1, col+1]];    
          }
        }
        if(row + 1 < 8 && col-1 > -1 && tiles[row+1][col-1].value !== null)
        {
          if(tiles[row+1][col-1].value.color !== color)
          {
            legalMovesPawn = [...legalMovesPawn, [row+1, col-1]];    
          }
        }
        if(row === 1)
        {
          if(tiles[row+1][col].value === null && tiles[row+2][col].value === null){
            legalMovesPawn = [...legalMovesPawn, [row+2, col]];
          }
        }
      }
      
    }
    return legalMovesPawn;
  }

  generateKnightCover = (row, col, color, tiles) =>
  {
    if(row === -1 && col === -1)
      return [];
    const legalMovesKnight = [  [row+2, col-1],
                                [row+2, col+1],
                                [row-2, col-1],
                                [row-2, col+1],
                                [row-1, col+2],
                                [row+1, col+2],
                                [row-1, col-2],
                                [row+1, col-2]];
    let knightCover = legalMovesKnight.filter((coord) => {
      if(coord[0] > 7 || coord[0] < 0 || coord[1] > 7 || coord[1] < 0)
      {
        return false;
      }

      if(tiles[coord[0]][coord[1]].value === null)
      {
        return true;
      }
      else if(tiles[coord[0]][coord[1]].value != null)
      {
        if(tiles[coord[0]][coord[1]].value.color != color)
        {
          return true;
        }else
        {
          return false;
        }
      }
    }); 
    return knightCover;
  }

  generateRookCover = (row, col, color, tiles) =>
  {
    if(row === -1 && col === -1)
      return [];
    let rookCover = [];
    let iOngoing = true;
    let jOngoing = true;
    let kOngoing = true;
    let lOngoing = true;
    if(row > 7 || row < 0 || col > 7 || col < 0)
    {
      return rookCover;
    }
    for(let i = row+1, j = row-1, k = col+1, l = col-1; i < 8 || j > -1 || k < 8 || l > -1; i++, j--, k++, l--)
    {
      const curr_i = [i, col];
      const curr_j = [j, col];
      const curr_k = [row, k];
      const curr_l = [row, l];
      
      if(iOngoing && i < 8)
      {
        if(tiles[curr_i[0]][curr_i[1]].value === null)
          {
            //if the currently examined tile is null we add it to the cover
            rookCover = [...rookCover, curr_i];
          }else if(tiles[curr_i[0]][curr_i[1]].value.color !== color){
              rookCover = [...rookCover, curr_i];
              iOngoing = false;
          }else
          {
            iOngoing = false;
          }
      }

      if(kOngoing && k < 8)
      {
        if(tiles[curr_k[0]][curr_k[1]].value === null)
        {
          rookCover = [...rookCover, curr_k];
        }else if(tiles[curr_k[0]][curr_k[1]].value.color !== color)
        {
          rookCover = [...rookCover, curr_k];
          kOngoing = false;
        }else{
          kOngoing = false;
        }
      }

      if(jOngoing && j > -1)
      {
        if(tiles[curr_j[0]][curr_j[1]].value === null)
        {
          rookCover = [...rookCover, curr_j];
        }else if(tiles[curr_j[0]][curr_j[1]].value.color !== color){
          rookCover = [...rookCover, curr_j];
          jOngoing = false;
        }else
        {
          jOngoing = false;
        }
      }

      if(lOngoing && l > -1)
      {
        if(tiles[curr_l[0]][curr_l[1]].value === null)
        {
          rookCover = [...rookCover, curr_l];
        }else if(tiles[curr_l[0]][curr_l[1]].value.color !== color){
          rookCover = [...rookCover, curr_l];
          lOngoing = false;
        }else
        {
          lOngoing = false;
        }
      }
    }
    return rookCover;
  }

  generateBishopCover = (row, col, color, tiles) =>
  {
    if(row === -1 && col === -1)
      return [];
    let bishopCover = [];
    let ijOngoing = true;
    let klOngoing = true; 
    for(let i = row+1, j = col+1, k = row-1, l = col-1; i < 8 || j < 8 || k > -1 || l > -1; i++, j++, k--, l--)
    {
      const curr_ij = [i, j];
      const curr_kl = [k, l];
      if(ijOngoing && (i < 8 && j < 8))
      {
        if(tiles[curr_ij[0]][curr_ij[1]].value === null)
        {
          bishopCover = [...bishopCover, curr_ij];
        }else if(tiles[curr_ij[0]][curr_ij[1]].value.color !== color)
        {
          bishopCover = [...bishopCover, curr_ij];
          ijOngoing = false;
        }else
        {
          ijOngoing = false;
        }
      }

      if(klOngoing && (k > -1 && l > -1))
      {
        if(tiles[curr_kl[0]][curr_kl[1]].value === null)
        {
          bishopCover = [...bishopCover, curr_kl];
        }else if(tiles[curr_kl[0]][curr_kl[1]].value.color !== color)
        {
          bishopCover = [...bishopCover, curr_kl];
          klOngoing = false;
        }else{
          klOngoing = false;
        }
      }
    }

    ijOngoing = true;
    klOngoing = true;
    for(let i = row+1, j = col-1, k = row-1, l = col+1; i < 8 || j > -1 || k > -1 || l < 8; i++, j--, k--, l++)
    {
      const curr_ij = [i, j];
      const curr_kl = [k, l];
      if(ijOngoing && (i < 8 && j > -1))
      {
        if(tiles[curr_ij[0]][curr_ij[1]].value === null)
        {
          bishopCover = [...bishopCover, curr_ij];
        }else if(tiles[curr_ij[0]][curr_ij[1]].value.color !== color)
        {
          bishopCover = [...bishopCover, curr_ij];
          ijOngoing = false;
        }else
        {
          ijOngoing = false;
        }
      }

      if(klOngoing && (k > -1 && l < 8))
      {
        if(tiles[curr_kl[0]][curr_kl[1]].value === null)
        {
          bishopCover = [...bishopCover, curr_kl];
        }else if(tiles[curr_kl[0]][curr_kl[1]].value.color !== color)
        {
          bishopCover = [...bishopCover, curr_kl];
          klOngoing = false;
        }else{
          klOngoing = false;
        }
      }
    }

    return bishopCover;
  }

  generateQueenCover = (row, col, color, tiles) =>
  {
    if(row === -1 && col === -1)
      return [];
    let queenCover = this.generateBishopCover(row,col,color,tiles).concat(this.generateRookCover(row,col,color,tiles));
    return queenCover;
  }

  generateKingCover = (row, col, color, tiles) =>
  {
    const legalMovesKing = [[row-1,col-1],
                            [row-1,col],
                            [row-1, col+1],
                            [row,col-1],
                            [row, col+1],
                            [row+1,col-1],
                            [row+1, col],
                            [row+1, col+1]];
    let kingCover = legalMovesKing.filter((coord) => {
      if(coord[0] > 7 || coord[0] < 0 || coord[1] > 7 || coord[0] < 0)
      {
        return false;
      }
      if(tiles[coord[0]][coord[1]].value === null)
      {
        return true;
      }
      else if(tiles[coord[0]][coord[1]].value != null)
      {
        if(tiles[coord[0]][coord[1]].value.color != color)
        {
          return true;
        }else
        {
          return false;
        }
      }
    }); 
    return kingCover;
  }

  //this method searches array for value and returns true if value is found in array
  searchArray(array, value)
  {
    const temp = [...array];
    let result = temp.find((coord) => JSON.stringify(coord) === JSON.stringify(value));
    if(typeof(result) === "undefined")
    {
      //the value was not found
      return false;
    }else
    {
      return true;
    }
  }

  //this method generates the cover for a piece given its row , col, color and ID based on given board configuration (tiles)
  generateCoverByID(row, col, color, tiles, ID)
  {
    let cover;
    if(row > 7 || row < 0 || col > 7 || col < 0)
    {
      return [];
    }
    switch(ID){
      case "pawn0":
      case "pawn1":
      case "pawn2":
      case "pawn3":
      case "pawn4":
      case "pawn5":
      case "pawn6":
      case "pawn7":
        cover = this.generatePawnCover(row, col, color, tiles);
        break;
      case "rook0":
      case "rook1":
        cover = this.generateRookCover(row, col, color, tiles);
        break;
      case "knight0":
      case "knight1":
        cover = this.generateKnightCover(row, col, color, tiles);
        break;
      case "bishop0":
      case "bishop1":
        cover = this.generateBishopCover(row, col, color, tiles);
        break;
      case "queen":
        cover = this.generateQueenCover(row, col, color, tiles);
        break;
      case "king":
        cover = this.generateKingCover(row, col, color, tiles);
        break;
      default:
    }
    return cover;
  }

  //this method generates the total cover of all the pieces of a player given the player's color, pieceSet and a board configuration
  generateCurrentCover(tiles, pieceSet, color)
  {
    let returnArray = [];
    const myIDs = ["pawn0", "pawn1", "pawn2", "pawn3", "pawn4", "pawn5", "pawn6", "pawn7", "rook0", "rook1", "knight0", "knight1", "bishop0", "bishop1", "queen", "king"];
    for(let i = 0; i < myIDs.length; i++)
    {
      const currPieceCoord = pieceSet[myIDs[i]];
      const curr_cover = this.generateCoverByID(currPieceCoord[0], currPieceCoord[1], color, tiles, myIDs[i]);
      returnArray = returnArray.concat(curr_cover);
    }

    return returnArray;
  }

  //the main algorithm for the program to control complex restrictions relating to king checks
  boardTest(board, curr_cover, current)
  {
    let returnArray = [];
    let pieceSet;
    let turnOpponent;

    //we initialize the proper piece set to generate the cover
    if(current.data.value.color === this.state.player)
    {
      //it is the player's turn so we want to generate cover for opponent
      const temp = this.state.opponentPieces;
      pieceSet = {pawn0: temp.pawn0, pawn1: temp.pawn1, pawn2: temp.pawn2, pawn3: temp.pawn3, pawn4: temp.pawn4, pawn5: temp.pawn5, pawn6:temp.pawn6, pawn7: temp.pawn7, rook0: temp.rook0, rook1: temp.rook1, knight0: temp.knight0, knight1: temp.knight1, bishop0: temp.bishop0, bishop1: temp.bishop1, queen: temp.queen, king: temp.king};
      turnOpponent = this.state.opponent;
    }else
    {
      //it is the opponent's turn so we want to generate cover for player
      const temp = this.state.playerPieces;
      pieceSet = {pawn0: temp.pawn0, pawn1: temp.pawn1, pawn2: temp.pawn2, pawn3: temp.pawn3, pawn4: temp.pawn4, pawn5: temp.pawn5, pawn6:temp.pawn6, pawn7: temp.pawn7, rook0: temp.rook0, rook1: temp.rook1, knight0: temp.knight0, knight1: temp.knight1, bishop0: temp.bishop0, bishop1: temp.bishop1, queen: temp.queen, king: temp.king};
      turnOpponent = this.state.player;
    }


    //we try each move that the selected piece can do and check to see if it will result in a check
    console.log("attempting to move " + current.data.value.color + " " + current.data.value.ID);
    for(let i = 0; i < curr_cover.length; i++)
    {
      const currentMove = curr_cover[i];
      console.log("currently testing for move to " + "[" + currentMove[0] + "," + currentMove[1] + "]");
      
      //below we generate the tile to test from, we need to do this since JavaScript uses pointers for object reference
      let tiles = [];
      for(let j = 0; j < board.length; j++)
      {
        let curr_row = [];
        for(let k = 0; k < board[j].length; k++)
        {
          const tempTile = {color: board[j][k].color, component:board[j][k].component, value: board[j][k].value, initColor: board[j][k].initColor};
          curr_row = [...curr_row, tempTile];
        }
        tiles = [...tiles, curr_row];
      }

      //here we reconfigure the tile based on the current move
      if(tiles[currentMove[0]][currentMove[1]].value === null)
      {
        //modify the tiles variable according to the move
        tiles[currentMove[0]][currentMove[1]] = {...tiles[currentMove[0]][currentMove[1]], value:current.data.value, component: current.data.component};
        tiles[current.row][current.col] = {...tiles[current.row][current.col], value: null, component: null};
      }else if(tiles[currentMove[0]][currentMove[1]].value.color != current.data.value.color)
      {
          //A capture scenario
          //we need to remove the captured piece from the piece set
          pieceSet[tiles[currentMove[0]][currentMove[1]].value.ID] = [-1,-1];
          //modify the tiles variable according to the move
          tiles[currentMove[0]][currentMove[1]] = {...tiles[currentMove[0]][currentMove[1]], value:current.data.value, component: current.data.component};
          tiles[current.row][current.col] = {...tiles[current.row][current.col], value: null, component: null};
      }
      //here we generate the cover of all the opponent's piece and concatenate them together to result based of the newly configured tiles
      const result = this.generateCurrentCover(tiles, pieceSet, turnOpponent);
      console.log("here is the opponent's cover after the move was made ");
      console.log(result);
      
      let kingCoord = null;
      if(current.data.value.color === this.state.player)
      {
        if(current.data.value.ID === "king")
        {
          kingCoord = currentMove;
        }else
        {
          kingCoord = this.state.playerPieces.king;  
        }
      }else{
        if(current.data.value.ID === "king")
        {
          kingCoord = currentMove;
        }else
        {
          kingCoord = this.state.opponentPieces.king;  
        }
      }
      
      if(!this.searchArray(result, kingCoord))
      {
        returnArray = [...returnArray, currentMove];
      }else
      {
        console.log("we have a check scenario from moving " + current.data.value.ID + " to [" + currentMove[0] + "," + currentMove[1] + "]");
      }
      //we reset the proper piece set to generate the cover
      if(current.data.value.color === this.state.player)
      {
        //it is the player's turn so we want to generate cover for opponent
        const temp = this.state.opponentPieces;
        pieceSet = {pawn0: temp.pawn0, pawn1: temp.pawn1, pawn2: temp.pawn2, pawn3: temp.pawn3, pawn4: temp.pawn4, pawn5: temp.pawn5, pawn6:temp.pawn6, pawn7: temp.pawn7, rook0: temp.rook0, rook1: temp.rook1, knight0: temp.knight0, knight1: temp.knight1, bishop0: temp.bishop0, bishop1: temp.bishop1, queen: temp.queen, king: temp.king};
        turnOpponent = this.state.opponent;
      }else
      {
        //it is the opponent's turn so we want to generate cover for player
        const temp = this.state.playerPieces;
        pieceSet = {pawn0: temp.pawn0, pawn1: temp.pawn1, pawn2: temp.pawn2, pawn3: temp.pawn3, pawn4: temp.pawn4, pawn5: temp.pawn5, pawn6:temp.pawn6, pawn7: temp.pawn7, rook0: temp.rook0, rook1: temp.rook1, knight0: temp.knight0, knight1: temp.knight1, bishop0: temp.bishop0, bishop1: temp.bishop1, queen: temp.queen, king: temp.king};
        turnOpponent = this.state.player;
      }
    }
    return returnArray;
  }

  tileClickHandler(row, col)
  {
    let history = [...this.state.history];
    const current = history[history.length - 1];
    const tiles = [...current.tiles];
    const target = tiles[row][col];
    const target_component = target.component;
    const target_value = target.value;

    let kingSelected = false;
    let curr_cover;
    let enterHighlightStage = false;

    if(this.state.current === null)
    {
      //we have not selected any pieces
      if(this.state.playerIsNext)
      {
        if(target_value != null)
        {
          if(target_value.color === this.state.player)
          {
            //player has selected a piece (target)
            this.setState({current: {data:target, row:row, col:col}});
            enterHighlightStage = true;
          }else
          {
            console.log("player attempted to move opponent piece");
            return;
          } 
        }
      }else
      {
        if(target_value != null)
        {
          if(target_value.color === this.state.opponent)
          {
            //opponent has selected a piece (target)
            this.setState({current: {data:target, row:row, col:col}});
            enterHighlightStage = true;
          }else
          {
            console.log("opponent attempted to move player piece");
            return;
          } 
        }
      }
      
      if(enterHighlightStage)
      {
        const m_piece = {data:target, row:row, col:col};
        //here we compute all the possible moves of the current piece
        switch(m_piece.data.value.piece)
        {
          case "pawn":
            curr_cover = this.generatePawnCover(m_piece.row, m_piece.col, m_piece.data.value.color, tiles);
            break;
          case "rook":
            curr_cover = this.generateRookCover(m_piece.row, m_piece.col, m_piece.data.value.color, tiles);
            break;
          case "knight":
            curr_cover = this.generateKnightCover(m_piece.row, m_piece.col, m_piece.data.value.color, tiles);
            break;
          case "bishop":
            curr_cover = this.generateBishopCover(m_piece.row, m_piece.col, m_piece.data.value.color, tiles);
            break;
          case "queen":
            curr_cover = this.generateQueenCover(m_piece.row, m_piece.col, m_piece.data.value.color, tiles);
            break;
          case "king":
            curr_cover = this.generateKingCover(m_piece.row, m_piece.col, m_piece.data.value.color, tiles);
            break;
          default:
            console.log("invalid piece name")
        }

        //Here we test each tile in curr_cover, if any of the moves would result in a check to the current player's king it is removed from curr_cover
        const filteredCover = this.boardTest(tiles, curr_cover, m_piece);

        //highlight the tiles within cover
        for(let i = 0; i < filteredCover.length; i++)
        {
          const curr_coord = filteredCover[i];
          const currTileContent = tiles[curr_coord[0]][curr_coord[1]]; 
          tiles[curr_coord[0]][curr_coord[1]] = {...currTileContent, color:"highlight square"};
        }

        history[history.length-1] = {...history[history.length-1], tiles:tiles};
        this.setState({history:history, highlightedTiles: filteredCover});
        
      }

    }else
    {
      //we have selected a piece
      //the reference to our piece
      /*
        structure of current:
          - {data:{value, component, color} , row:row, col:col}
          - we can omit color within data since it is the color of the tile and should not be modified
      */
      const current = this.state.current;
      const cover = this.state.highlightedTiles;
      if(!this.searchArray(cover,[row,col]))
      {
         //reset the tile highlighting
        const tempHighlight = this.state.highlightedTiles;
        for(let i = 0; i < tempHighlight.length; i++)
        {
          const curr_coord = tempHighlight[i];
          const currTileContent = tiles[curr_coord[0]][curr_coord[1]]; 
          const m_color = currTileContent.initColor;
          tiles[curr_coord[0]][curr_coord[1]] = {...currTileContent, color: m_color};
        }
        history[history.length-1] = {...history[history.length-1], tiles:tiles};
        this.setState({
          history: history,
          current: null,
          highlightedTiles: [],
        });
        return;
      }
      
      if(target_value === null)
      {
        //move the piece to an empty tile
        //here we set the target tile parameters
        tiles[row][col] = {...tiles[row][col], value:current.data.value, component: current.data.component};
        //here we set the old tile parameters
        tiles[current.row][current.col] = {...tiles[current.row][current.col], value: null, component: null};
      }else if(target_value.color != current.data.value.color)
      {
          //we only capture if the target is of different color
          //first we add the captured piece to the capturedPieces state property
          const capturedPieces = this.state.capturedPieces;
          this.setState({capturedPieces: [...capturedPieces, target_component]});
          //here we set the target tile parameters to the current value
          tiles[row][col] = {...tiles[row][col], value:current.data.value, component: current.data.component};
          //here we set the old tile parameters to be null
          tiles[current.row][current.col] = {...tiles[current.row][current.col], value: null, component: null};
      }

      if(kingSelected)
      {
        if(current.data.value.color === this.state.player)
        {
          this.setState({playerKing: [row,col]});
        }else
        {
          this.setState({opponentKing: [row,col]});
        }
      }

      //update the playerPieces or opponentPieces state
      let movedPieces;
      let oppPieces;
      if(current.data.value.color === this.state.player)
      {
        movedPieces = {...this.state.playerPieces};
        movedPieces[current.data.value.ID] = [row, col];
        this.setState({playerPieces:movedPieces});
        if(target_value !== null)
        {
          oppPieces = {...this.state.opponentPieces};
          oppPieces[target_value.ID] = [-1,-1];
          this.setState({opponentPieces: oppPieces})
        }
      }else
      {
        movedPieces = {...this.state.opponentPieces};
        movedPieces[current.data.value.ID] = [row,col];
        this.setState({opponentPieces:movedPieces});
        if(target_value !== null)
        {
          oppPieces = {...this.state.playerPieces};
          oppPieces[target_value.ID] = [-1,-1];
          this.setState({playerPieces: oppPieces})
        }
      }
      
      //reset the tile highlighting
      const tempHighlight = this.state.highlightedTiles;
      for(let i = 0; i < tempHighlight.length; i++)
      {
        const curr_coord = tempHighlight[i];
        const currTileContent = tiles[curr_coord[0]][curr_coord[1]]; 
        const m_color = currTileContent.initColor;
        tiles[curr_coord[0]][curr_coord[1]] = {...currTileContent, color: m_color};
      }

      //always reset current to null after each successful move
      this.setState({
          history: [...history, {tiles:tiles}],
          playerIsNext: !this.state.playerIsNext,
          stepNumber: history.length,
          current: null,
          highlightedTiles: [],
        });
    }
  }

  testFunction = () => {
    return [1,2,3];
  }

  testHandler = () =>
  {
    let test = [[1,2],[3,4]];
    if(this.searchArray(test,[1,2]))
    {
      console.log("the value was found");
    }
    
    
  }
  /*
    test notes:
    - we can return arrays from functions
    - JSON.stringify([1,2]) returns "[1,2]"

  */

  

  render(){
    const history = this.state.history;
    const current = history[this.state.stepNumber];
    const rank = this.state.rank;
    const selected = this.state.current;

    let currentPiece = null;
    let currentPlayer = null;
    let cover = null;
    if(selected != null)
    {
      currentPiece = selected.data.value.ID;
      currentPlayer = selected.data.value.color;
      cover = this.generateCoverByID(selected.row, selected.col, currentPlayer, current.tiles, currentPiece);

    }

    return(
      <div className="game">
        <TestDisplay piece={currentPiece} player={currentPlayer} cover={cover} playerPieces={this.state.playerPieces} opponentPieces={this.state.opponentPieces}/>
        <CapturedPieces captured={this.state.capturedPieces}/>
        <TurnIndicator playerTurn={this.state.playerIsNext}/>
        <CheckDisplay check={this.state.playerCheck || this.state.opponentCheck}/>
        <div className="game-board">
          <Board tiles={current.tiles} rank={rank} player={this.state.player} onClick={(row,col) => this.tileClickHandler(row,col)}/>
        </div>
        <button onClick={this.testHandler}>Test Button</button>
      </div>
    );
  }
}

export default Game;


